@keyframes fadeIn {
   from { opacity: 0; }
   to { opacity: 1; }
 }

.myInput{
   display: block;
   width: 100%;
   padding: 5px 10px;
   background-color: transparent;
   color: var(--text-color);
   border: 2px solid var(--icons-color);
   border-radius: 5px;
   margin: 10px auto;
   animation: fadeIn 2s  
}
